import React from 'react';

function ComplexityRating({rating}) {

    const complexity = Number(rating)

    return (
        <div className='course-complexity'>
            <span style={{marginRight: '.5rem'}}>Сложность: </span>
            <i className={complexity >= 1 ? "fa-solid fa-star" : "fa-regular fa-star"}></i>
            <i className={complexity >= 2 ? "fa-solid fa-star" : "fa-regular fa-star"}></i>
            <i className={complexity >= 3 ? "fa-solid fa-star" : "fa-regular fa-star"}></i>
            <i className={complexity >= 4 ? "fa-solid fa-star" : "fa-regular fa-star"}></i>
            <i className={complexity >= 5 ? "fa-solid fa-star" : "fa-regular fa-star"}></i>
        </div>
    );
}

export default ComplexityRating;