export const headerSvgVariants = {
    hidden: {
        opacity: 0,
    },
    visible: {
        opacity: 1,
        transition: {
            staggerChildren: .25,
            ease: 'easeIn',
            type: 'spring'
        }
    },
}

export const headerPathVariants = {
    hidden: {
        opacity: 1,
        pathLength: 0,
    },
    visible: {
        opacity: 1,
        pathLength: 1,
        transition: {
            duration: 2
        }
    }
}

export const divLeftHorizontalVariants = {
    hidden: {
        opacity: 0,
        x: '-500%',
    },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            duration: .25,
            staggerChildren: .25,
            ease: 'easeInOut',
            type: 'spring'
        }
    },
}

export const divRightHorizontalVariants = {
    hidden: {
        opacity: 0,
        x: '500%',
    },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            delay: 1.5,
            duration: .25,
            staggerChildren: .25,
            ease: 'easeInOut',
            type: 'spring'
        }
    },
}

