import React, {useEffect, useState} from 'react';
import {Link, useParams} from 'react-router-dom';

import {client} from '../lib/client';

function CourseScreen() {
    const { slug } = useParams();

    const [courses, setCourses] = useState([])
    const [error, setError] = useState(false)

    useEffect(() => {
        client.fetch(`*[_type == "course"]`)
            .then((data) => {
                setCourses(data)
            })
            .catch((err) => {
                setError(true)
                console.log('err: ', err)
            })
    }, [])

    const course = courses.find((course) => course.slug.current === slug)

    let descriptionArr = course?.courseDescription?.split(';')

    if (!course) return (
        <div>Loading...</div>
    )

    if (error) return (
        <div>Error =(</div>
    )

    return (
        <div id='courseScreen'>
            <div className="container">

                <div className='course-name'>{course.name}</div>

                {
                    console.log(course.keywords.split(';')[0])
                }

                <div className="course-screen-desc">
                    {
                        (course && descriptionArr) && descriptionArr.map((desc) => (
                            <div className='item-description' key={descriptionArr.indexOf(desc)}>{desc}</div>
                        ))
                    }
                </div>

{/*                To run a development server, use the [npm run dev] or    |*/}
{/*│  [npx sanity dev] command instead. For more information, */}

                <div className="frame-container">
                    <iframe
                        className='course-frame'
                        src={course.sourceLink}
                        frameBorder='0'
                        border='0'
                        allowFullScreen='1'
                    />
                </div>



            </div>
        </div>
    );
}

export default CourseScreen;