import React, {useState, useEffect} from 'react';
import {Link} from "react-router-dom";
import {urlFor} from "../lib/client";

import ComplexityRating from "./ComplexityRating";

import {motion} from "framer-motion";
import {divRightHorizontalVariants} from "./animations";

function Course({course}) {
    const [desc, setDesc] = useState(false)
    let descriptionArr = course.courseDescription

    useEffect(() => {
        if (descriptionArr.includes(';')) {
            setDesc(true)
        }
    }, [desc, descriptionArr])


    return (
        <motion.div
            variants={divRightHorizontalVariants}
            initial='hidden' animate='visible'
            className='course-card' id={`course-card-${course._id}`}
        >

            <motion.div className='course-name' variants={divRightHorizontalVariants}>
                {course.name}
            </motion.div>

            <Link to={`/course/${course.slug.current}`}>
                <div className="card-picture">
                    <img className='picture' src={urlFor(course.image)} alt=''/>
                </div>
            </Link>

            <div className="course-description">

                <Link to={`/course/${course.slug.current}`} className="course-title">
                    {course.name}
                </Link>

                <ComplexityRating rating={course.courseComplexity}/>
                <div className="item-description">{desc ? descriptionArr.split(';')[0] : descriptionArr}</div>
                <div className="course-content">{course.courseContent}</div>
            </div>
        </motion.div>
    );
}

export default Course;