import React, {useState, useEffect} from 'react';

import {motion} from "framer-motion";
import {divLeftHorizontalVariants} from "./animations";

function Filter({setActiveFilter, activeFilter, setFiltered, courses}) {

    let filter = courses

    const [btnFilter, setBtnFilter] = useState(false)
    const [searchFilter, setSearchFilter] = useState(false)
    const [sliderFilter, setSliderFilter] = useState(false)

    const [activeSearchFilter, setActiveSearchFilter] = useState('')
    const [rangeFilter, setRangeFilter] = useState('0')

    const filterInput = (keyWord, data) => {
        if (!keyWord) {
            return data
        } else {
            return data.filter((course) =>
                course.keywords.toLowerCase().includes(keyWord.toLowerCase())
            )
        }
    }

    const filterBtn = (filter, data) => {
        if (filter === '0') {
            return data
        } else {
            return data.filter((course) =>
                course.courseContent === `${filter}`
            )
        }
    }

    const filterRange = (range, data) => {
        if (range === '0') {
            return data
        } else {
            return data.filter((course) =>
                course.courseComplexity === `${range}`
            )
        }
    }


    useEffect(() => {
        filter = filterBtn(activeFilter, filter)
        filter = filterRange(rangeFilter, filter)
        const Debounce = setTimeout(() => {
            const courses = filterInput(activeSearchFilter, filter)
            setFiltered(courses)
        }, 500)

        return () => clearTimeout(Debounce)
    }, [activeFilter, activeSearchFilter, rangeFilter])


    return (
        <motion.div variants={divLeftHorizontalVariants} initial='hidden' animate='visible' className='courses-filter'>
            <motion.div variants={divLeftHorizontalVariants} className="filter-inner">
                <motion.div variants={divLeftHorizontalVariants} className="filter-btns-container">
                    <motion.button variants={divLeftHorizontalVariants}
                        className={activeFilter === "0" ? "active-btn filter-btn" : "filter-btn"}
                        onClick={() => {
                            setActiveFilter("0")
                            setBtnFilter(true)
                        }}
                    >Все курсы</motion.button>
                    <motion.button variants={divLeftHorizontalVariants}
                        className={activeFilter === "Английский" ? "active-btn filter-btn" : "filter-btn"}
                        onClick={() => {
                            setActiveFilter("Английский")
                            setBtnFilter(true)
                        }}
                    >Английский</motion.button>
                    <motion.button variants={divLeftHorizontalVariants}
                        className={activeFilter === "Арабский" ? "active-btn filter-btn" : "filter-btn"}
                        onClick={() => {
                            setActiveFilter("Арабский")
                            setBtnFilter(true)
                        }}
                    >Арабский</motion.button>
                </motion.div>

                <motion.div className="key-word-container">
                    <motion.div>Поиск:</motion.div>
                    <motion.input value={activeSearchFilter} type="text" onChange={(e) => setActiveSearchFilter(e.target.value)}/>
                </motion.div>

                <div className='range-container'>
                    <span>Сложность</span>
                    <input
                        className='range'
                        type="range"
                        value={rangeFilter}
                        min={0} max={5} step={1}
                        onChange={(e) => setRangeFilter(e.target.value.toString())}/>
                    <div className="stars-container">
                        <i className={rangeFilter >= 1 ? "range-star fa-solid fa-star" : "range-star fa-regular fa-star"}></i>
                        <i className={rangeFilter >= 2 ? "range-star fa-solid fa-star" : "range-star fa-regular fa-star"}></i>
                        <i className={rangeFilter >= 3 ? "range-star fa-solid fa-star" : "range-star fa-regular fa-star"}></i>
                        <i className={rangeFilter >= 4 ? "range-star fa-solid fa-star" : "range-star fa-regular fa-star"}></i>
                        <i className={rangeFilter >= 5 ? "range-star fa-solid fa-star" : "range-star fa-regular fa-star"}></i>
                    </div>
                </div>
            </motion.div>
        </motion.div>
    );
}

export default Filter;